.pill-button {
  display: inline-flex;
  position: relative;
  padding: 0 1.75rem;
  height: 2.75rem;
  text-align: center;
  text-decoration: none;
  align-items: center;
  justify-content: center
}

.pill-button.is-big {
  padding: 0 2.5rem;
  height: 3.5rem
}


.pill-button .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%
}

.pill-button .background .left {
  position: absolute;
  top: 0;
  left: -15px;
  right: 50%;
  height: 100%;
  overflow: hidden
}

.pill-button .background .left .plain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top-left-radius: 35px;
  border-bottom-left-radius: 35px;
  background: rgba(255,255,255,.047);
  transform: translate(12px);
  transition: transform .2s;
  will-change: transform
}

.pill-button .background .right {
  position: absolute;
  top: 0;
  right: -15px;
  left: 50%;
  height: 100%;
  overflow: hidden
}

.pill-button .background .right .plain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top-right-radius: 35px;
  border-bottom-right-radius: 35px;
  background: rgba(255,255,255,.047);
  transform: translate(-12px);
  transition: transform .2s;
  will-change: transform
}

.pill-button .label {
  display: flex;
  align-items: center;
  position: relative
}

.experience {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh
}

.sections {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
  pointer-events: none
}

html.is-vertical.is-focused .sections {
  pointer-events: auto
}

.sections.is-scrollable {
  overflow-y: auto
}

.section {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none
}

.section.is-crew {
  --section-color: #a48bff
}

.section.is-privatekey {
  --section-color: #85a5ff
}

.section.is-onscout {
  --section-color: #fff
}

.section.is-isonline {
  --section-color: #f2acf2
}

.section.is-ideasby {
  --section-color: #f9d77f
}

.section.is-takemeto {
  --section-color: #fff
}

.section *::selection {
  background: var(--section-color)
}

.section *::-moz-selection {
  background: var(--section-color)
}

.section .reveal {
  transition: opacity .3s,transform .3s
}

html.is-vertical .section.is-active,.section.is-active .content * {
  pointer-events: auto
}

.section.is-active .reveal {
  opacity: 1;
  transform: translate(0);
  transition-timing-function: ease-out,ease-out
}

.section.is-active .reveal.delay-in-0 {
  transition-delay: .3s
}

.section.is-active .reveal.delay-in-1 {
  transition-delay: .35s
}

.section.is-active .reveal.delay-in-2 {
  transition-delay: .4s
}

.section.is-active .reveal.delay-in-3 {
  transition-delay: .45s
}

.section.is-active .reveal.delay-in-4 {
  transition-delay: .5s
}

.section.is-active .reveal.delay-in-5 {
  transition-delay: .55s
}

.section.is-active .reveal.delay-in-6 {
  transition-delay: .6s
}

.section.is-active .reveal.delay-in-7 {
  transition-delay: .65s
}

.section.is-active .reveal.delay-in-8 {
  transition-delay: .7s
}

.section.is-active .reveal.delay-in-9 {
  transition-delay: .75s
}

.section.is-active .reveal.delay-in-10 {
  transition-delay: .8s
}

.section.is-active .focus-point {
  transform: scale(0);
  transition-delay: 0s
}

.section:not(.is-active) .reveal {
  opacity: 0;
  transform: translate(60px);
  transition-timing-function: ease-in,ease-in
}

.section:not(.is-active) .reveal.delay-out-0 {
  transition-delay: 0s
}

.section:not(.is-active) .reveal.delay-out-1 {
  transition-delay: .05s
}

.section:not(.is-active) .reveal.delay-out-2 {
  transition-delay: .1s
}

.section:not(.is-active) .reveal.delay-out-3 {
  transition-delay: .15s
}

.section:not(.is-active) .reveal.delay-out-4 {
  transition-delay: .2s
}

.section:not(.is-active) .reveal.delay-out-5 {
  transition-delay: .25s
}

.section:not(.is-active) .reveal.delay-out-6 {
  transition-delay: .3s
}

.section:not(.is-active) .reveal.delay-out-7 {
  transition-delay: .35s
}

.section:not(.is-active) .reveal.delay-out-8 {
  transition-delay: .4s
}

.section:not(.is-active) .reveal.delay-out-9 {
  transition-delay: .45s
}

.section:not(.is-active) .reveal.delay-out-10 {
  transition-delay: .5s
}

.section.is-content {
  text-align: left
}

.section.is-content .content {
  display: flex;
  min-height: 100vh;
  margin-right: 5%;
  margin-left: 50%;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center
}

html.is-vertical .section.is-content .content {
  display: block;
  margin-right: 0;
  margin-left: 0;
  padding-top: 60vh;
  padding-right: 5%;
  padding-left: 5%;
  padding-bottom: 14vh
}

.section.is-content .intro {
  margin-bottom: 60px
}


.section.is-content .intro .title {
  width: 80%;
  margin-bottom: .75em;
  font-family: "Sequel 100 Wide";
  font-size: 2em;
  line-height: 1.25em
}



.section.is-content .intro .description {
  width: 90%;
  line-height: 1.6em
}



.section.is-content .features {
  display: flex;
  width: 100%
}

.section.is-content .features .feature {
  width: calc(25% - 15px);
  margin-right: 15px
}

.section.is-content .features .feature .icon-container {
  position: relative;
  margin-bottom: 10px;
  background: #1a1a1a;
  border-radius: 16px
}

.section.is-content .features .feature .icon-container .ratio {
  padding-top: 100%
}

.section.is-content .features .feature .icon-container .icon-inner {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center
}



.section.is-content .features .feature .label {
  line-height: 1.4em;
  font-size: .85em;
  color: #fffb
}

.section.is-content .main-link {
  margin-top: 9vmin;
  letter-spacing: 1px;
  font-weight: 700;
  color: #000
}



.section.is-content .main-link .background .left .plain,.section.is-content .main-link .background .right .plain {
  background: var(--section-color)
}

.section .focus-point {
  position: absolute;
  top: 50%;
  left: -150px;
  width: 150px;
  height: 150px;
  margin-top: -50px;
  margin-left: -50px;
  cursor: pointer;
  pointer-events: auto;
  transform: scale(1);
  transition: transform .3s cubic-bezier(.5,0,.5,1);
  transition-delay: .3s
}



.section .focus-point .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: var(--section-color);
  transform: scale(.53);
  transition: transform .3s cubic-bezier(.6,-.45,.75,.9);
  transition-delay: 0s;
  will-change: transform
}

.section .focus-point .plus-icon {
  position: absolute;
  top: calc(50% - 15px);
  left: calc(50% - 15px);
  width: 30px;
  height: 30px;
  opacity: 1;
  transition: opacity .3s;
  transition-delay: .2s;
  will-change: opacity
}

.section .focus-point .plus-icon:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: calc(50% - 2px);
  width: 4px;
  height: 100%;
  background: #000
}

.section .focus-point .plus-icon:after {
  content: "";
  display: block;
  position: absolute;
  top: calc(50% - 2px);
  left: 0;
  width: 100%;
  height: 4px;
  background: #000
}

.section .focus-point .label {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 20px;
  color: #000;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity .3s;
  transition-delay: 0s;
  will-change: opacity
}

.scroll-down-container {
  display: flex;
  position: fixed;
  left: 0;
  width: 100%;
  bottom: 6%;
  pointer-events: none;
  align-items: center;
  justify-content: center
}

.scroll-down {
  pointer-events: none;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity .6s 0s ease-in,transform .6s 0s ease-in;
  will-change: opacity,transform
}

.scroll-down.is-visible {
  pointer-events: auto;
  opacity: 1;
  transform: translateY(0);
  transition: opacity .6s ease-out,transform .6s ease-out
}

.scroll-down .icon {
  position: relative;
  margin-right: 1.25rem
}

.scroll-down .icon .circle {
  position: absolute;
  top: -.5rem;
  left: -.5rem;
  width: 1rem;
  height: 1rem;
  border: 2px solid #fff;
  border-radius: .5rem;
  animation: scroll-down-icon-circle 4s infinite;
  transform-origin: 0% 0%
}

